interface IDropDownOptions {
  uid?: string;
  label: string;
  value: string;
  code?: string;
  name?: string;
}

interface ITaskAdvanceSearch {
  applicationNumber: {
    uid: string;
    name: string;
  };
  customerName: {
    uid: string;
    name: string;
  };
  assignedTo: {
    uid: string;
    name: string;
  };
  stage: string;
  stageStatus: string;
  salesManager: {
    uid: string;
    name: string;
  };
  creditManager: {
    uid: string;
    name: string;
  };
  initiatedOnGreaterThan: string;
  initiatedOnLessThan: string;
  product: string;
  state: {
    uid: string;
    name: string;
  };
  branch: {
    uid: string;
    name: string;
  };
  initiatedBy: {
    uid: string;
    name: string;
    userId?: string;
  };
  lendingType: string;
  bankFinancier: {
    uid: string;
    name: string;
  };
  loanAmountGreaterThan: string;
  loanAmountLessThan: string;
  daysPendingGreaterThan: string;
  daysPendingLessThan: string;
}

interface ITaskAdvanceSearchDropdownList {
  product?: IDropDownOptions[];
  lendingType?: IDropDownOptions[];
}

const filterOptions = [
  {
    name: SEARCH_OPTION.QUICK_SEARCH.label,
    value: SEARCH_OPTION.QUICK_SEARCH.value,
    // logo: positiveTrendIcon,
    //className: toggleClassChannelsAdvanced,
  },
  {
    name: SEARCH_OPTION.ADVANCE_SEARCH.label,
    value: SEARCH_OPTION.ADVANCE_SEARCH.value,
    //logo: negativeTrendIcon,
    // className: toggleClassChannelsAdvanced,
  },
];

const ASSIGN_TO = {
  ME: "ME",
  ALL: "ASSIGNED_TO_ALL",
};

const initialAdvanceSearchObj: ITaskAdvanceSearch = {
  applicationNumber: {
    uid: "",
    name: "",
  },
  customerName: {
    uid: "",
    name: "",
  },
  assignedTo: {
    uid: "",
    name: "",
  },
  stage: "",
  stageStatus: "",
  salesManager: {
    uid: "",
    name: "",
  },
  creditManager: {
    uid: "",
    name: "",
  },
  initiatedOnGreaterThan: "",
  initiatedOnLessThan: "",
  product: "",
  state: {
    uid: "",
    name: "",
  },
  branch: {
    uid: "",
    name: "",
  },
  initiatedBy: {
    uid: "",
    name: "",
  },
  lendingType: "",
  bankFinancier: {
    uid: "",
    name: "",
  },
  loanAmountGreaterThan: "",
  loanAmountLessThan: "",
  daysPendingGreaterThan: "",
  daysPendingLessThan: "",
};

const PageTaskList = () => {
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */

  // Logic For Both Quick Search And Advance Search
  const [searchMode, setSearchMode] = useState(SEARCH_OPTION.QUICK_SEARCH.value);
  const [quickSearchValues, setQuickSearchValues] = useState<any>(["ALL", ASSIGN_TO.ME]);
  const [advancedSearchValues, setAdvancedSearchValues] =
    useState<ITaskAdvanceSearch>(initialAdvanceSearchObj);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<any>([
    { name: "", uid: "", userId: "" },
  ]);
  const [dataTable, setDataTable] = useState({
    limit: 10,
    loading: false,
    totalRecords: 0,
    start: 0,
    search: ``,
    runCustomQuery: false,
    records: [],
  });

  const [productDropdownOptions, setProductDropdownOptions] = useState<IDropDownOptions[]>([]);
  const [lendingTypeDropdownOptions, setLendingTypeDropdownOptions] = useState<IDropDownOptions[]>(
    []
  );
  // error handling states
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right"); //NOSONAR
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  const userProfile = useSelector((state: any) => state.UserProfileReducers.profileObj);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  /*
    -------------------------------------------------------
      refs
    -------------------------------------------------------
  */
  const dataTableRef = useRef<IReadonlyTableRef | null>(null);
  const toastMessageRef = useRef(null);

  /*
    -------------------------------------------------------
      Hooks: useEffect
    -------------------------------------------------------
  */

  useEffect(() => {
    init();
  }, []);

  useEffect(() => {
    handleSearch();
  }, [quickSearchValues]);

  useEffect(() => {
    if (dataTableRef && dataTableRef.current) {
      dataTableRef?.current?.search();
    }
  }, [dataTable.search]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /* 
    ---------------------------------------------------------------------------------------------------
    Validators:
    ---------------------------------------------------------------------------------------------------
   */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  const init = async () => {
    getProductOptions();
    getLendingTypeOptions();
  };

  const getProductOptions = async () => {
    try {
      const res = await ProductConfigProductService.getAll(0, 30, "");
      if (validateGetAPIResponse(res)) {
        updateDropdownOptions("product", res?.data?.results);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const getLendingTypeOptions = async () => {
    try {
      const res = await StaticMasterDataService.getAll(
        0,
        30,
        `staticMasterType.code=PRODUCT_LENDING_TYPE`
      );
      if (validateGetAPIResponse(res)) {
        updateDropdownOptions("lendingType", res?.data?.results);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  /*
    -------------------------------------------------------
      Handlers: Event handler for onChange, onBlur etc
    -------------------------------------------------------
  */

  const handleChangeFilterType = (e: any) => {
    setSearchMode(e.value);
    setQuickSearchValues(["ALL", ASSIGN_TO.ME]);
    setAdvancedSearchValues(initialAdvanceSearchObj);
  };

  const handleChangePipelineFilter = (value: string, index: any) => {
    // Add or remove the value from the array based on its presence
    if (value !== "ALL" && !quickSearchValues.includes(value)) {
      setQuickSearchValues(
        [...quickSearchValues, value].filter((searchVal: any) => searchVal !== "ALL")
      );
    } else if (quickSearchValues.includes(value)) {
      setQuickSearchValues(quickSearchValues.filter((searchVal: any) => searchVal !== value));
    } else {
      setQuickSearchValues(
        [...quickSearchValues, value].filter(
          (searchVal) =>
            searchVal === ASSIGN_TO.ME || searchVal === ASSIGN_TO.ALL || searchVal === "ALL"
        )
      );
    }
  };

  const handleChangeAssignToFilter = (value: string) => {
    if (value === ASSIGN_TO.ME && !quickSearchValues.includes(value)) {
      setQuickSearchValues(
        [...quickSearchValues, value].filter((searchVal: any) => searchVal !== ASSIGN_TO.ALL)
      );
    } else if (value === ASSIGN_TO.ALL && !quickSearchValues.includes(value)) {
      setQuickSearchValues(
        [...quickSearchValues, value].filter((searchVal: any) => searchVal !== ASSIGN_TO.ME)
      );
    }
  };

  const handleAdvanceSearchAutoCompleteChange = (event: any) => {
    const { name, value } = event.target;
    let fieldValue = "";
    if (typeof value === "object") {
      if (value.lanId) fieldValue = value.lanId;
      else if (value.fullName) fieldValue = value.fullName;
      else if (value.name) fieldValue = value.name;
      setAdvancedSearchValues((prev) => ({
        ...prev,
        [name]: { name: fieldValue, uid: value.uid, userId: value.userId },
      }));
    } else {
      setAdvancedSearchValues((prev) => ({
        ...prev,
        [name]: { name: value, uid: "", userId: "" },
      }));
    }
  };

  const handleAdvanceSearchDropdownChange = (e: DropdownChangeParams) => {
    setAdvancedSearchValues((prev: any) => {
      return {
        ...prev,
        [e.target.name]: e.value,
      };
    });
  };

  const handleAdvanceSearchInputChange = (event: any) => {
    const { value } = event.target;
    setAdvancedSearchValues((prev: any) => ({
      ...prev,
      [event.target.name]: value,
    }));
  };

  const handleAmountChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    const unformattedValue: number | string = currencyFormatToNumberForFilter(value ?? "");
    setAdvancedSearchValues((prevFilterObj) => ({
      ...prevFilterObj,
      [name]:
        typeof unformattedValue === "number" ? numberFormatter(unformattedValue, "en-IN", 0) : "",
    }));
  };

  /* 
    -------------------------------------------------------
      ButtonClick Functions:
    -------------------------------------------------------
  */

  const onReset = () => {
    resetValidator();
    setAdvancedSearchValues(initialAdvanceSearchObj);
    setDataTable({
      ...dataTable,
      search: "",
    });
  };

  const handleSearch = () => {
    if (searchMode === SEARCH_OPTION.QUICK_SEARCH.value) {
      triggerQuickSearch();
    } else if (searchMode === SEARCH_OPTION.ADVANCE_SEARCH.value) {
      triggerAdvanceSearch();
    }
  };

  /* 
    -------------------------------------------------------
      Helper Functions:
    -------------------------------------------------------
  */

  const updateDropdownOptions = (key: keyof ITaskAdvanceSearchDropdownList, options: any[]) => {
    if (key === "product") {
      setProductDropdownOptions(options ?? []);
    } else if (key === "lendingType") {
      setLendingTypeDropdownOptions(options ?? []);
    }
  };

  const searchAutoCompleteLoanService = async (event: any) => {
    let suggestionsTemplate = [];
    await LoanService.getAll(0, 10, `&lanId:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteCustomerName = async (event: any) => {
    let suggestionsTemplate = [];
    await ApplicantService.getCustomerName(0, 10, `&fullName:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteAssignedTo = async (event: any) => {
    let suggestionsTemplate = [];
    await ProfileService.findAssignee(0, 10, `&name:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteSalesManager = async (event: any) => {
    let suggestionsTemplate = [];
    await UserService.getEmployee(0, 10, `${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteCreditManager = async (event: any) => {
    let suggestionsTemplate = [];
    await UserService.getEmployee(0, 10, `${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteState = async (event: any) => {
    let suggestionsTemplate = [];
    await StateService.getAll(0, 10, `name:lk=${event.query}&`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }

    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteBranch = async (event: any) => {
    let suggestionsTemplate = [];
    await OrgStructureBranchService.getAll(0, 10, `name:lk=${event.query}&`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteInitiatedBy = async (event: any) => {
    let suggestionsTemplate = [];
    await UserService.getEmployee(0, 10, `${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteBank = async (event: any) => {
    let suggestionsTemplate = [];
    await BankMasterDataService.getAll(0, 10, `&name:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const triggerQuickSearch = () => {
    let search = "";

    if (quickSearchValues.includes("ALL")) {
      const allOptions = QUICK_SEARCH_PIPELINE_OPTIONS.map((option) => {
        if (option.value !== "ALL") return option.value;
      });
      search = `&stage.stageName:in=[${allOptions}]`;
    } else {
      const searchValuesWithoutAssignee = quickSearchValues.filter(
        (searchVal: any) => searchVal !== ASSIGN_TO.ALL && searchVal !== ASSIGN_TO.ME
      );
      search = `&stage.stageName:in=[${searchValuesWithoutAssignee}]`;
    }
    if (quickSearchValues.includes(ASSIGN_TO.ME)) {
      search = `${search}&stage.assignee.uid=${userProfile.uid}`;
    }

    search = `${search}&stage.status:in=[${LOAN_STAGE_STATUS.INITIATED},${LOAN_STAGE_STATUS.RE_OPEN},${LOAN_STAGE_STATUS.IN_REVIEW}]`;

    search = `${search} &sort={"createdAt":"DESC"}`;

    setDataTable({
      ...dataTable,
      search,
    });
  };

  const triggerAdvanceSearch = () => {
    if (!validateForm()) return;

    let search = "";
    const currentSystemDate = parsedDateFromAPIValue(getLocalStorage(LOCAL_STORAGE.SYSTEM_DATE));

    if (advancedSearchValues.applicationNumber?.name) {
      search = `${search}&loan.uid=${advancedSearchValues.applicationNumber?.uid}`;
    }
    if (advancedSearchValues.customerName?.name) {
      search = `${search}&stage.applicant.uid=${advancedSearchValues.customerName?.uid}`;
    }
    if (advancedSearchValues.stage) {
      search = `${search}&stage.stageName=${advancedSearchValues.stage}`;
    }
    if (advancedSearchValues.stageStatus) {
      search = `${search}&stage.status=${advancedSearchValues.stageStatus}`;
    }
    if (advancedSearchValues.salesManager?.name) {
      search = `${search}&loan.salesManager.uid=${advancedSearchValues.salesManager?.uid}`;
    }
    if (advancedSearchValues.creditManager?.name) {
      search = `${search}&loan.creditManager.uid=${advancedSearchValues.creditManager?.uid}`;
    }
    if (advancedSearchValues.initiatedOnGreaterThan) {
      search = `${search}&stage.createdAtSystemDate:ge=${convertDateToISO(
        advancedSearchValues.initiatedOnGreaterThan
      )}`;
    }
    if (advancedSearchValues.initiatedOnLessThan) {
      search = `${search}&stage.createdAtSystemDate:le=${convertDateToISO(
        advancedSearchValues.initiatedOnLessThan
      )}`;
    }
    if (advancedSearchValues.product) {
      search = `${search}&loan.product.uid=${advancedSearchValues.product}`;
    }
    if (advancedSearchValues.state?.name) {
      search = `${search}&loan.state.uid=${advancedSearchValues.state?.uid}`;
    }
    if (advancedSearchValues.branch?.name) {
      search = `${search}&loan.branch.uid=${advancedSearchValues.branch?.uid}`;
    }
    if (advancedSearchValues.initiatedBy?.name) {
      search = `${search}&stage.createdBy=${advancedSearchValues.initiatedBy?.userId}`;
    }
    if (advancedSearchValues.lendingType) {
      search = `${search}&loan.incomeSchemeProduct.name:lk=${advancedSearchValues.lendingType}`;
    }
    if (advancedSearchValues.bankFinancier?.name) {
      search = `${search}&loan.bankFinancier.uid=${advancedSearchValues.bankFinancier?.uid}`;
    }
    if (currencyFormatToNumberForFilter(advancedSearchValues?.loanAmountLessThan)) {
      search = `${search}&loan.amount:le=${currencyFormatToNumberForFilter(
        advancedSearchValues?.loanAmountLessThan
      )}`;
    }
    if (currencyFormatToNumberForFilter(advancedSearchValues?.loanAmountGreaterThan)) {
      search = `${search}&loan.amount:ge=${currencyFormatToNumberForFilter(
        advancedSearchValues?.loanAmountGreaterThan
      )}`;
    }

    if (Number(advancedSearchValues.daysPendingGreaterThan) > 0) {
      const currDate = new Date(currentSystemDate);
      currDate.setDate(currDate.getDate() - Number(advancedSearchValues.daysPendingGreaterThan));
      search = `${search}&stage.createdAtSystemDate:lt=${currDate.toISOString()}`;
      if (!search.includes(`&stage.status=${STAGE_STATUS.INITIATED.name}`)) {
        search = `${search}&stage.status=${STAGE_STATUS.INITIATED.name}`;
      }
    }

    if (Number(advancedSearchValues.daysPendingLessThan) > 0) {
      const currDate = new Date(currentSystemDate);
      currDate.setDate(currDate.getDate() - Number(advancedSearchValues.daysPendingLessThan));
      search = `${search}&stage.createdAtSystemDate:gt=${currDate.toISOString()}`;
      if (!search.includes(`&stage.status=${STAGE_STATUS.INITIATED.name}`)) {
        search = `${search}&stage.status=${STAGE_STATUS.INITIATED.name}`;
      }
    }

    search = `${search}&sort={"createdAt":"DESC"}`;

    setDataTable({
      ...dataTable,
      search,
    });
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */

  const renderLoanApplicationNo = (rowData: any) => {
    const noStageUidRoutes = NON_STAGE_MAPPING[rowData?.stage?.stageName];
    const path = STAGE_MAPPING[rowData?.stage?.stageName] || "";
    const URL =
      linkBasePath +
      linkAuthRoute +
      LINK_LOAN_DETAIL +
      path +
      "/" +
      rowData?.loan?.uid +
      "/" +
      `${!noStageUidRoutes ? rowData?.stage?.uid + "/" : ""}`;

    return (
      <a
        className="flex items-center underline text-primary-sapphireBlue-900"
        href={URL}
        onClick={() => {
          setLocalStorage(LOCAL_STORAGE.STAGE_DETAIL, JSON.stringify(rowData?.stage));
        }}
      >
        {rowData?.loan?.lanId ?? "-"}
      </a>
    );
  };

  const renderProduct = (rowData: any) => {
    return (
      <div className="flex items-center" title={rowData?.loan?.product?.name}>
        {rowData?.loan?.product?.name ?? "-"}
      </div>
    );
  };

  const renderState = (rowData: any) => {
    return <div className="flex items-center">{rowData?.state}</div>;
  };

  const renderBranch = (rowData: any) => {
    return <div className="flex items-center">{rowData?.loan?.branch?.name ?? "-"}</div>;
  };

  const renderCustomerName = (rowData: any) => {
    return (
      <div className="flex items-center" title={rowData?.stage?.applicant?.fullName}>
        {rowData?.stage?.applicant?.fullName ?? "-"}
      </div>
    );
  };

  const renderStage = (rowData: any) => {
    return <div className="flex items-center w-40">{convertEnumValueFromUpperCaseToFirstLetterUpperCase(rowData?.stage?.stageName) ?? "-"}</div>;
  };

  const renderStatus = (rowData: any) => {
    return <div className="flex items-center">{rowData?.stage?.status}</div>;
  };

  const renderLendingType = (rowData: any) => {
    return (
      <div className="flex items-center">{rowData?.loan?.product?.lendingType?.name ?? "-"}</div>
    );
  };

  const renderBankFinancier = (rowData: any) => {
    return (
      <div className="flex items-center" title={rowData?.loan?.bankFinancier?.name}>
        {rowData?.loan?.bankFinancier?.name ?? "-"}
      </div>
    );
  };

  const renderInitiatedOn = (rowData: any) => {
    return <div className="flex items-center">{formatDate(rowData?.createdAtSystemDate)}</div>;
  };

  const renderInitiatedBy = (rowData: any) => {
    return <div className="flex items-center">{rowData?.stage?.createdByUserName ?? "-"}</div>;
  };

  const renderDaysPending = (rowData: any) => {
    return <div className="flex items-center">{rowData?.daysPending ?? "-"}</div>;
  };

  const renderAssignedTo = (rowData: any) => {
    return <div className="flex items-center">{rowData?.stage?.assignee?.name ?? "-"}</div>;
  };

  const renderLoanAmount = (rowData: any) => {
    return <div className="flex justify-end">{currencyFormatter(rowData?.loan?.amount) || 0}</div>;
  };

  const renderSalesManager = (rowData: any) => {
    return <div className="flex items-center">{rowData?.loan?.salesManager?.name ?? "-"}</div>;
  };

  const renderCreditManager = (rowData: any) => {
    return <div className="flex items-center">{rowData?.loan?.creditManager?.name ?? "-"}</div>;
  };

  const itemApplicantNumberTemplate = (item: any) => {
    if (item.lanId === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.lanId}`} />
      </div>
    );
  };

  const itemCustomerTemplate = (item: any) => {
    if (item.fullName === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.fullName}`} />
      </div>
    );
  };

  const itemAssignedToTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemSalesManagerTemplate = (item: any) => {
    if (item.name === undefined || item?.name.length < 1) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemCreditManagerTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemStateTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
        <Text className="text-secondary-gray-500 text-xxs" label={item?.country} />
      </div>
    );
  };

  const itemBranchTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemInitiatedByTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemBankFinancierTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  return (
    <div className="p-3 rounded-masterDataPageRadius bg-white flex flex-col gap-3 overflow-scroll screen-body-scroll">
      <div className="px-3 flex w-full justify-between items-center">
        <div className="py-5">
          <Text className="text-primary-ptext-1000 font-bold text-2xl " label="heading.task" />
        </div>

        <div className="  w-78.5 ">
          <SelectButtonBox
            optionLabel="name"
            optionValue="value"
            value={searchMode}
            options={filterOptions}
            onChange={handleChangeFilterType}
          />
        </div>
      </div>

      {searchMode === SEARCH_OPTION.QUICK_SEARCH.value ? (
        <div className="p-3 bg-primary-sapphireBlue-100 rounded-lg flex flex-col gap-3">
          <Text
            className="text-primary-ptext-1000 font-bold text-base"
            label="label.task.pipeline"
          />
          <div className="flex flex-col gap-3">
            <div className="flex gap-3">
              {QUICK_SEARCH_PIPELINE_OPTIONS.slice(0, 4).map((filter, index) => (
                <SimpleChip
                  key={filter.label}
                  label={filter.label}
                  isSelected={quickSearchValues.includes(filter.value)}
                  onClick={() => handleChangePipelineFilter(filter.value, index)}
                />
              ))}
            </div>

            <div className="flex gap-3">
              {QUICK_SEARCH_PIPELINE_OPTIONS.slice(4, 7).map((filter, index) => (
                <SimpleChip
                  key={filter.label}
                  label={filter.label}
                  isSelected={quickSearchValues?.includes(filter.value)}
                  onClick={() => handleChangePipelineFilter(filter.value, index)}
                />
              ))}
            </div>

            <div className="flex gap-3">
              {QUICK_SEARCH_PIPELINE_OPTIONS.slice(7, 10).map((filter, index) => (
                <SimpleChip
                  key={filter.label}
                  label={filter.label}
                  isSelected={quickSearchValues?.includes(filter.value)}
                  onClick={() => handleChangePipelineFilter(filter.value, index)}
                />
              ))}
            </div>

            <div className="flex flex-wrap gap-3">
              {QUICK_SEARCH_PIPELINE_OPTIONS.slice(10, 15).map((filter, index) => (
                <SimpleChip
                  key={filter.label}
                  label={filter.label}
                  isSelected={quickSearchValues?.includes(filter.value)}
                  onClick={() => handleChangePipelineFilter(filter.value, index)}
                />
              ))}
            </div>
          </div>

          <Text
            label={"label.task.assignedTo"}
            className="text-primary-ptext-1000 font-bold text-base"
          />
          <div className="pb-2 flex gap-3">
            <SimpleChip
              label={"label.task.me"}
              isSelected={quickSearchValues?.includes(ASSIGN_TO.ME)}
              onClick={() => handleChangeAssignToFilter(ASSIGN_TO.ME)}
            />
            <SimpleChip
              label={"label.task.all"}
              isSelected={quickSearchValues?.includes(ASSIGN_TO.ALL)}
              onClick={() => handleChangeAssignToFilter(ASSIGN_TO.ALL)}
            />
          </div>
        </div>
      ) : (
        <div className="p-3 bg-primary-sapphireBlue-100 rounded-lg flex flex-col gap-6">
          <Text
            className="  text-primary-ptext-1000 font-bold text-base"
            label="label.task.advancedSearch"
          />

          <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
            <AutoComplete
              label="label.task.applicationNumber"
              name="applicationNumber"
              field="applicationNumber"
              value={advancedSearchValues.applicationNumber?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteLoanService}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemApplicantNumberTemplate}
            />
            <AutoComplete
              label="label.task.customerName"
              name="customerName"
              field="customerName"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={advancedSearchValues.customerName?.name}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteCustomerName}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemCustomerTemplate}
            />
            <AutoComplete
              label="label.task.assignedTo"
              name="assignedTo"
              field="assignedTo"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={advancedSearchValues.assignedTo?.name}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteAssignedTo}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemAssignedToTemplate}
            />
            <SimpleDropDown
              label="label.task.stage"
              name="stage"
              value={advancedSearchValues.stage}
              options={STAGE_NAME}
              optionLabel={"label"}
              optionValue={"value"}
              onChange={handleAdvanceSearchDropdownChange}
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
            />

            <SimpleDropDown
              label={"label.task.stageStatus"}
              name="stageStatus"
              value={advancedSearchValues.stageStatus}
              options={STAGE_STATUS_OPTIONS}
              optionLabel={"label"}
              optionValue={"value"}
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              onChange={handleAdvanceSearchDropdownChange}
            />

            <AutoComplete
              label="label.task.salesManager"
              name="salesManager"
              field="salesManager"
              value={advancedSearchValues.salesManager?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteSalesManager}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemSalesManagerTemplate}
            />
            <AutoComplete
              label="label.task.creditManager"
              name="creditManager"
              field="creditManager"
              value={advancedSearchValues.creditManager?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteCreditManager}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemCreditManagerTemplate}
            />
            <SystemDatePicker
              variant={INPUT_STYLE_TYPE.PRIMARY}
              label="label.partnerTask.initiatedOnGreaterThan"
              placeholder="DD-MMM-YYYY"
              name="initiatedOnGreaterThan"
              value={advancedSearchValues.initiatedOnGreaterThan}
              onChange={handleAdvanceSearchInputChange}
            />
            <SystemDatePicker
              variant={INPUT_STYLE_TYPE.PRIMARY}
              placeholder="DD-MMM-YYYY"
              label="label.partnerTask.initiatedOnLessThan"
              name="initiatedOnLessThan"
              value={advancedSearchValues.initiatedOnLessThan}
              onChange={handleAdvanceSearchInputChange}
            />
            <SimpleDropDown
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              label="label.task.product"
              name="product"
              options={productDropdownOptions}
              optionValue="uid"
              optionLabel="name"
              value={advancedSearchValues.product}
              onChange={handleAdvanceSearchDropdownChange}
            />
            <AutoComplete
              label="label.task.state"
              name="state"
              field="state"
              value={advancedSearchValues.state?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteState}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemStateTemplate}
            />

            <AutoComplete
              label="label.task.branch"
              name="branch"
              field="branch"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={advancedSearchValues.branch?.name}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteBranch}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemBranchTemplate}
            />
            <AutoComplete
              label="label.task.initiatedBy"
              name="initiatedBy"
              field="initiatedBy"
              variant={INPUT_STYLE_TYPE.PRIMARY}
              value={advancedSearchValues.initiatedBy?.name}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteInitiatedBy}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemInitiatedByTemplate}
            />
            <SimpleDropDown
              label="label.task.lendingType"
              variant={DROPDOWN_STYLE_TYPE.SECONDARY}
              name="lendingType"
              options={lendingTypeDropdownOptions}
              optionLabel="name"
              optionValue="name"
              value={advancedSearchValues.lendingType}
              onChange={handleAdvanceSearchDropdownChange}
            />
            <AutoComplete
              label="label.task.bank/Financier"
              name="bankFinancier"
              field="bankFinancier"
              value={advancedSearchValues.bankFinancier?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={searchAutoCompleteBank}
              onChange={handleAdvanceSearchAutoCompleteChange}
              itemTemplate={itemBankFinancierTemplate}
            />
            <InputBox
              label="input.sourceDetails.loanAmountGreaterThan"
              name="loanAmountGreaterThan"
              value={advancedSearchValues.loanAmountGreaterThan}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAmountChange}
            />
            <InputBox
              label="input.sourceDetails.loanAmountGreaterThan"
              name="loanAmountLessThan"
              value={advancedSearchValues.loanAmountLessThan}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAmountChange}
            />
            <InputBox
              label="label.task.headingPendingGreaterThan"
              name="daysPendingGreaterThan"
              value={advancedSearchValues.daysPendingGreaterThan}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAdvanceSearchInputChange}
              onBlur={validate}
              validation={simpleValidator?.current?.message(
                `daysPendingGreaterThan`,
                advancedSearchValues.daysPendingGreaterThan,
                "checkNumber|onlyPositive|onlyInteger"
              )}
            />
            <InputBox
              label="label.task.headingPendingLessThan"
              name="daysPendingLessThan"
              value={advancedSearchValues.daysPendingLessThan}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAdvanceSearchInputChange}
              onBlur={validate}
              validation={simpleValidator?.current?.message(
                `daysPendingLessThan`,
                advancedSearchValues.daysPendingLessThan,
                "checkNumber|onlyPositive|onlyInteger"
              )}
            />

            <div className="flex justify-end">
              <div className="flex items-center gap-10">
                <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onReset} />
                <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={handleSearch} />
              </div>
            </div>
          </div>
        </div>
      )}

      <div className="p-3 flex w-full justify-between items-center">
        <Text
          className="text-primary-ptext-1000 font-bold text-2xl "
          label={"label.task.myApplications"}
        />
      </div>

      <ReadOnlyTable
        standardDataFormatPromise={StageService.getStages}
        dataKey="id"
        search={dataTable.search}
        ref={dataTableRef}
      >
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.loanApplicationNo")}
          body={renderLoanApplicationNo}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.product")}
          body={renderProduct}
          style={{ minWidth: "200px", width: "auto" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.state")}
          body={renderState}
          style={{ minWidth: "150px", width: "auto" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.branch")}
          body={renderBranch}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.partnerTask.customerName")}
          body={renderCustomerName}
          style={{ minWidth: "200px", width: "auto" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.partnerTask.stage")}
          body={renderStage}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.task.status")}
          body={renderStatus}
        />
        <Column
          className="text-primary-ptext-1000 text-right text-xs py-3 pl-6"
          header={useTranslateText("label.task.loanAmount")}
          body={renderLoanAmount}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.lendingType")}
          body={renderLendingType}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.bankFinancier")}
          body={renderBankFinancier}
          style={{ minWidth: "250px", width: "auto" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.task.initiatedOn")}
          body={renderInitiatedOn}
          style={{ minWidth: "150px" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.task.initiatedBy")}
          body={renderInitiatedBy}
          style={{ minWidth: "150px" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.task.daysPending")}
          body={renderDaysPending}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.task.assignedTo")}
          body={renderAssignedTo}
          style={{ minWidth: "150px" }}
        />

        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.salesManager")}
          body={renderSalesManager}
          style={{ minWidth: "150px" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6 pr-3"
          header={useTranslateText("label.loanListing.creditManager")}
          body={renderCreditManager}
          style={{ minWidth: "150px" }}
        />
      </ReadOnlyTable>

      <ToastCustom
        position={position}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default PageTaskList;
